import React, { Component } from "react";
import Slider from "react-slick";
import { Box, Button, Container, Grid, Typography } from "@material-ui/core";
import "./Team3.css";
import { teamData } from "./../HomePage/data";
import { RenderSectionHeading } from "../common/commonComponent";
import { useStyles } from "./BodyStyles";
import ScrollAnimation from "react-animate-on-scroll";

export default function Team3() {
    const classes = useStyles();
  const settings = {
    autoplay: true,
    autoplaySpeed: 2000,
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 2,
  };
  return (
    <Box className={classes.sectionDark} id="Team">
    <ScrollAnimation animateIn="fadeIn">
      <Grid
        container
        style={{
          displa: "flex",
          justifyContent: "center",
          alignContent: "center",
        }}
      >
        <Grid item xs={12} sm={8}>
          {RenderSectionHeading({
            smallText: "Team",
            heading: "Let's See Us",
            alignCenter: true,
          })}
        </Grid>
      </Grid>
    <Box>
      <Slider {...settings}>
      {teamData.map((item) => (
        <div>
          <Box className="main">
            <Box className="profile-card">
              <Box className="img">
                {/* <img src="data:image/jpeg;base64," /> */}
                <img src={item.image} />
              </Box>
              <Box className="caption">
                {/* <h3>Vin Diesel</h3> */}
                <Typography variant="h3">{item.nameData}</Typography>
                {/* <p>Senior App Developer</p> */}
                <Typography variant="p"> {item.statusData} </Typography>
                <Box className="social-links">
                  <a href="#">
                    <i className="fab fa-facebook"></i>
                  </a>
                  <a href="#">
                    <i className="fab fa-instagram"></i>
                  </a>
                  <a href="#">
                    <i className="fab fa-twitter"></i>
                  </a>
                </Box>
              </Box>
            </Box>
          </Box>
        </div>
        
        ))}
      </Slider>
    </Box>
    </ScrollAnimation>
    </Box>
  );
}
